@keyframes op {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

li {
  list-style: none;
  padding: 1em; }

#findBG {
  animation: op .3s;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(30, 32, 36, 0.7); }
  #findBG .dialog {
    position: relative;
    display: flex;
    flex-direction: column;
    box-shadow: 0 10px 20px #1e2024;
    border-radius: 10px;
    width: 65%;
    height: 65%;
    background-color: #32393d;
    padding: 12px;
    padding-right: 0; }
    #findBG .dialog > span {
      font-size: 29px;
      font-weight: 700;
      padding: 10px;
      color: white;
      text-align: center; }
    #findBG .dialog > div {
      width: 100%;
      display: flex;
      flex: 1; }
      #findBG .dialog > div > a:nth-child(1) > .grow {
        background: linear-gradient(-45deg, #4c87af, #4ca1af); }
      #findBG .dialog > div > a:nth-child(2) > .grow {
        background: linear-gradient(-45deg, #396afc, #2948ff); }
      #findBG .dialog > div > a:nth-child(3) > .grow {
        background-color: rgba(255, 143, 73, 0.6); }
      #findBG .dialog > div > a {
        cursor: pointer;
        flex: 1;
        position: relative;
        height: 100%;
        margin-right: 10px;
        border-radius: 10px;
        padding: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: flex 0.5s; }
        #findBG .dialog > div > a > .group {
          height: min-content;
          z-index: 100;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          transition: opacity 0.2s; }
          #findBG .dialog > div > a > .group span {
            text-align: center;
            line-height: 1.4; }
            #findBG .dialog > div > a > .group span:nth-child(1) {
              font-weight: 700;
              color: white;
              font-size: 29px; }
            #findBG .dialog > div > a > .group span:nth-child(2) {
              color: #cacaca;
              font-size: 21px; }
        #findBG .dialog > div > a > .grow {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          top: 100%;
          border-radius: 10px;
          background-color: #fff;
          transition: top 0.2s;
          z-index: 11; }
        #findBG .dialog > div > a:hover > .grow {
          top: 0; }
    #findBG .dialog > i {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #1e2024;
      cursor: pointer;
      transition: background-color .2s, box-shadow .2s;
      color: white;
      position: absolute;
      font-size: 20px;
      right: 0;
      top: 0;
      z-index: 20;
      transform: translate(50%, -50%); }
      #findBG .dialog > i:hover {
        background-color: #353940;
        box-shadow: 0 2px 4px #1e2024; }
