.link-active {
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 15px;
  color: white;
  text-decoration: none;
  border-radius: 6px;
  padding: 2px 0;
  margin: 2px 0;
  display: flex;
  width: 100%;
  align-items: center;
  cursor: default; }
  .link-activeIcon {
    font-size: 20px;
    padding: 10px;
    margin: 0 10px 0 10px;
    color: #ff9a60; }

.link {
  font-size: 15px;
  color: #cacaca;
  text-decoration: none;
  padding: 2px 0;
  margin: 2px 0;
  display: flex;
  width: 100%;
  align-items: center; }
  .linkIcon {
    font-size: 20px;
    padding: 10px;
    margin: 0 10px 0 10px;
    color: #cacaca; }
  .link:hover {
    background-color: #1e2024;
    cursor: pointer;
    color: white;
    border-radius: 6px; }
  .link:hover > .linkIcon {
    color: #ff9a60; }
